$mainColor: #444;
$mainBackground: #fff;

$container: 1170px;
$colGups: 15px;

$textFont-size: 1rem; // 16px
$linkColor: blue;

$breakpoints: (
        xs: 0,
        sm: 575px,
        md: 768px,
        lg: 1025px,
        xl: 1200px,
        xxl: 1440px
);

$columns: 12;
$col: $columns;

@mixin col($col, $columns) {
  width: percentage($col/$columns);
}

@mixin col-create() {
  @for $i from 1 through $columns {
    .col-#{$i}-12 {
      @include col($i, $columns);
    }
  }
}

.container {
  max-width: $container;
  width: 100%;
  margin: 0 auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -$colGups;
  margin-left: -$colGups;
}

[class^='col'] {
  padding-left: $colGups;
  padding-right: $colGups;
  width: 100%;
  min-height: 1px;
}

@include col-create();

body {
  font-family: sans-serif;
  font-size: 1rem;
}

a, button {
  transition: .3s ease-in;
  transition-property: color, background-color, opacity;

  svg {
    fill: currentColor;
  }
}

button, .button {
  cursor: pointer;
}

.menu{
  display: flex;
}

